.share {
  display: inline-block;
  &-backdrop {
    position: fixed;
    z-index: 9;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: $control-bg-inactive-color;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: $bg-color;
    border-radius: $control-border-radius;
    padding: 13px 15px;
  }
  &-buttons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px;
    width: 100%;

    li {
      width: 43px;
      height: 43px;
      box-sizing: border-box;
      padding: 5px;
      display: inline-block;
      a {
        display: inline-block;
      }
    }
  }

  .share-menu-direct {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    span {
      flex: 0;
      padding-right: 10px;
    }
    input {
      flex: 1;
      border: 1px solid $bg-darker-color;
    }
    button {
      border: none;
      background: none;
      cursor: pointer;
      svg {
        height: 14px;
        width: 14px;

      }
    }
  }
}
