







































































































































































@import "../../styles/style.scss";

.home {
  display: block;
}

.categories {
  &-label {
    color: $font-color-darker;
    font-weight: 600;
    margin-right: 0.5em;
  }
  margin-bottom: 7px;
}

.types {
  &-label {
    color: $font-color-darker;
    font-weight: 600;
    margin-right: 0.5em;
  }
}

.moar-button {
  margin: 0 auto;
  font-weight: normal;
  .moar-icon {
    margin-right: 14px;
  }

  .moar-text {
    font-weight: normal;
    font-size: 28px;
  }
}

.filters-panel {
  margin: 0 auto 9px;
  text-align: left;
  max-width: $container-max-width;
  @media (min-width: $break-sm) {
    margin: 0 auto 30px;
    text-align: center;
  }
}

.panel-header {
  // background-color: darken($bg-color, 10%);
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 6px 0;
  user-select: none;
  font-weight: 600;
  border-radius: $control-border-radius $control-border-radius 0 0;
  @media (min-width: $break-sm) {
    display: none;
  }

  &-button {
    cursor: pointer;
    display: flex;
    align-items: center;

    stroke: $control-bg-color;
    color: $control-bg-color;

    &:hover,
    &:active {
      color: $control-bg-hover-color;
      stroke: $control-bg-hover-color;
    }
  }
}

.panel-title {
  padding-right: 5px;
  font-size: 13px;
  text-decoration: underline;
}

.panel-icon {
  display: flex;
  align-content: center;
  width: 19px;
  height: 19px;
  margin-right: 7px;
}

.panel-content {
  padding: 0 0 1rem;
  @media (min-width: $break-sm) {
    background: transparent;
    padding: 0;
  }
  &.panel-collapsed {
    height: 0;
    overflow: hidden;
    padding: 0;
    @media (min-width: $break-sm) {
      padding: inherit;
      height: inherit;
    }
  }
}

.panel-spacer {
  flex: 1 1 auto;
}

.categories-label,
.types-label {
  font-size: 13px;
}
